import React, { Component }  from 'react'; 
import GCListlargeScreen from './greenClusterList'
import GCSmallScreen from './greenClusterListSmallSCreen'

export default class GCListMain extends Component  {
  render() {
        let width = window.innerWidth;  
    if (width > 768) {
      return (
       <GCListlargeScreen></GCListlargeScreen>
       
       );
    } else {
      return ( 
    <GCSmallScreen></GCSmallScreen>
      );
    }
 }
 
}
